const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectSI = "/dashboard/sales/sales-invoices";
const redirectRaptorSI = "/dashboard/raptor/sales-invoices";
const redirectPosSI = "/dashboard/pos/sales-invoices";

const expandedRowRender = data => {
  const columns = [
    {
      title: "No",
      dataIndex: "index_no",
      width: "2%"
    },
    {
      title: getLanguage({ id: "items.code" }),
      dataIndex: "item_code",
      width: "15%"
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "30%",
      render: text => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: (
        <Tooltip placement="top" title={getLanguage({ id: "SIQty" })}>
          <a href="javascript:;">{getLanguage({ id: "SIQtys" })}</a>
        </Tooltip>
      ),
      dataIndex: "qty", // dari PR
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={parseFloat(text)} />
    },
    {
      title: getLanguage({ id: "price" }) + " (IDR)",
      dataIndex: "item_price", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        return (
          <NumberFormat
            value={parseFloat(row.currency_rate) * parseFloat(text)}
          />
        );
      }
    },
    {
      title: getLanguage({ id: "discount" }) + " (IDR)",
      dataIndex: "sub_total_discount", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        return (
          <NumberFormat
            value={parseFloat(row.currency_rate) * parseFloat(text)}
          />
        );
      }
    },
    {
      title: getLanguage({ id: "sidebar.tax" }) + " (IDR)",
      dataIndex: "sub_total_tax", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        return (
          <NumberFormat
            value={parseFloat(row.currency_rate) * parseFloat(text)}
          />
        );
      }
    },
    {
      title: getLanguage({ id: "subTotal" }) + " (IDR)",
      dataIndex: "sub_total", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        return (
          <NumberFormat
            value={parseFloat(row.currency_rate) * parseFloat(text)}
          />
        );
      }
    }
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="purchaseInvoices.importPR.itemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={false}
        style={{ marginBottom: "5px", marginTop: "5px" }}
      />
    </div>
  );
};

class SalesInvoiceDetailReport extends Component {
  state = {
    filter: {
      id_customer: null,
      id_location: null,
      payment_status: null,
      status: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "transaction_date",
      sort_type: "asc"
      // pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getSalesInvoiceDetailReports } = this.props;
    getSalesInvoiceDetailReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.salesInvoiceDetailReport.error) {
      notification("error", this.props.salesInvoiceDetailReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.salesInvoiceDetailReport.success) {
      notification("success", this.props.salesInvoiceDetailReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getSalesInvoiceDetailReports } = this.props;
    let filter = this.filterValue();

    getSalesInvoiceDetailReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getSalesInvoiceDetailReports } = this.props;
    this.setState({
      filter: {
        id_customer: null,
        id_location: null,
        payment_status: null,
        status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc"
        // pagination: null
      }
    });
    this.setState({ search: null });
    getSalesInvoiceDetailReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.payment_status) {
      string
        ? (string += "&payment_status=" + filter.payment_status)
        : (string += "payment_status=" + filter.payment_status);
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_customer) {
      string
        ? (string += "&id_customer=" + filter.id_customer)
        : (string += "id_customer=" + filter.id_customer);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getSalesInvoiceDetailReports } = this.props;
    //const { current_page } = this.props.salesInvoiceDetailReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getSalesInvoiceDetailReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getSalesInvoiceDetailReports } = this.props;
    this.setState({
      filter: {
        id_customer: null,
        id_location: null,
        status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc"
        // pagination: null
      }
    });
    this.setState({ search: null });
    getSalesInvoiceDetailReports();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateSalesInvoiceReport(id);
    } else {
      this.props.deactivateSalesInvoiceReport(id);
    }
  };

  // // Handle pagination
  // handlePageChange = page => {
  //   const { getSalesInvoiceDetailReports } = this.props;
  //   const { search, filter } = this.props.salesInvoiceDetailReport;
  //   getSalesInvoiceDetailReports(page, search, filter);
  // };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "sales_invoice_no",
        rowKey: "sales_invoice_no",
        width: "9%",
        // fixed: "left",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "pending";
              break;
            case 2:
              className = "processed";
              break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "pending";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>
                <Link
                  to={`${
                    row.transaction_type === 21
                      ? redirectRaptorSI
                      : row.transaction_type === 31
                      ? redirectPosSI
                      : redirectSI
                  }/update/${row.id_sales_invoice}`}
                >
                  {text}
                </Link>
              </TransNo>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "date" }),
        data: true,
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "9%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "8.5%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "8%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "salesOrders.totalQty" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        //width: "15%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalDiscount" }) + " (IDR)",
        dataIndex: "total_discount",
        rowKey: "total_discount",
        //width: "15%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalTax" }) + " (IDR)",
        dataIndex: "total_tax",
        rowKey: "total_tax",
        //width: "15%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "salesOrders.totalFreightCost" }) + " (IDR)",
        dataIndex: "total_freight_cost",
        rowKey: "total_freight_cost",
        //width: "15%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalAmount" }) + " (IDR)",
        dataIndex: "total_amount",
        rowKey: "total_amount",
        //width: "15%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "6%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "processedBy" }),
        dataIndex: "processed_by",
        rowKey: "processed_by",
        width: "6%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "paymentStatus" }),
        dataIndex: "payment_status_string",
        rowKey: "payment_status_string",
        width: "6%",
        align: "center",
        render: (text, row) => {
          let className;
          let display;
          switch (row.payment_status) {
            case 0:
              className = "new-payment";
              display = getLanguage({ id: "new" });
              break;
            case 1:
              className = "billed-payment";
              display = getLanguage({ id: "billed" });
              break;
            case 2:
              className = "paid-payment";
              display = getLanguage({ id: "paid" });
              break;
            default:
              className = "new-payment";
              display = getLanguage({ id: "new" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status_string",
        rowKey: "status_string",
        width: "6%",
        align: "center",
        render: (text, row) => {
          let className;
          let display;
          switch (row.status) {
            case 1:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
            case 2:
              className = "processed";
              display = getLanguage({ id: "processed" });
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const excelColumns = [
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "sales_invoice_no",
        rowKey: "sales_invoice_no",
        width: "9%"
      },
      {
        title: getLanguage({ id: "date" }),
        date: true,
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "9%"
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "8.5%"
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "8%"
      },
      {
        title: getLanguage({ id: "salesOrders.totalQty" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        //width: "15%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalDiscount" }) + " (IDR)",
        dataIndex: "total_discount",
        rowKey: "total_discount",
        //width: "15%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalTax" }) + " (IDR)",
        dataIndex: "total_tax",
        rowKey: "total_tax",
        //width: "15%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "salesOrders.totalFreightCost" }) + " (IDR)",
        dataIndex: "total_freight_cost",
        rowKey: "total_freight_cost",
        //width: "15%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalAmount" }) + " (IDR)",
        dataIndex: "total_amount",
        rowKey: "total_amount",
        //width: "15%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "6%"
      },
      {
        title: getLanguage({ id: "processedBy" }),
        dataIndex: "processed_by",
        rowKey: "processed_by",
        width: "6%"
      },
      {
        title: getLanguage({ id: "paymentStatus" }),
        dataIndex: "payment_status_string",
        rowKey: "payment_status_string",
        width: "6%",
        align: "center"
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status_string",
        rowKey: "status_string",
        width: "6%",
        align: "center"
      },
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code",
        width: "15%"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "30%"
      },
      {
        title: getLanguage({ id: "SIQty" }),
        dataIndex: "qty", // dari PR
        width: "10%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "price" }) + " (IDR)",
        dataIndex: "item_price", // dari PR
        width: "10%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "discount" }) + " (IDR)",
        dataIndex: "sub_total_discount", // dari PR
        width: "10%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "sidebar.tax" }) + " (IDR)",
        dataIndex: "sub_total_tax", // dari PR
        width: "10%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "subTotal" }) + " (IDR)",
        dataIndex: "sub_total", // dari PR
        width: "10%",
        align: "right",
        parse: true
      }
    ];

    // Data setup
    const { salesInvoiceDetailReport } = this.props;
    const {
      sales_invoice_detail_reports,
      locations,
      customers,
      filter,
      filter_select
    } = salesInvoiceDetailReport;

    const dataSource = [];
    let expandedRowKeys = [];
    let excelDataSource = [];

    if (
      sales_invoice_detail_reports &&
      sales_invoice_detail_reports.length > 0
    ) {
      // for table and print's datasource
      Object.keys(sales_invoice_detail_reports).map((item, index) => {
        sales_invoice_detail_reports[index].sales_invoice_details.map(
          data =>
            (data.currency_rate =
              sales_invoice_detail_reports[index].currency_rate)
        );
        return dataSource.push({
          ...sales_invoice_detail_reports[index],
          key: index
        });
      });

      // for print's expandedRow
      Object.keys(sales_invoice_detail_reports).map((item, index) => {
        return expandedRowKeys.push(index);
      });

      // for excel export
      sales_invoice_detail_reports.map((item, index) => {
        let payment_status_string;
        switch (item.payment_status) {
          case 0:
            payment_status_string = getLanguage({ id: "new" });
            break;
          case 1:
            payment_status_string = getLanguage({ id: "billed" });
            break;
          case 2:
            payment_status_string = getLanguage({ id: "paid" });
            break;
          default:
            payment_status_string = getLanguage({ id: "new" });
            break;
        }
        let status_string;
        switch (item.status) {
          case 1:
            status_string = getLanguage({ id: "pending" });
            break;
          case 2:
            status_string = getLanguage({ id: "processed" });
            break;
          case 3:
            status_string = getLanguage({ id: "cancelled" });
            break;
          default:
            status_string = getLanguage({ id: "new" });
            break;
        }

        // item.total_discount =
        //   parseFloat(item.currency_rate) * parseFloat(item.total_discount);
        // item.total_tax =
        //   parseFloat(item.currency_rate) * parseFloat(item.total_tax);
        // item.total_freight_cost =
        //   parseFloat(item.currency_rate) * parseFloat(item.total_freight_cost);
        // item.total_amount =
        //   parseFloat(item.currency_rate) * parseFloat(item.total_amount);

        item.sales_invoice_details.map((data, i) => {
          // data.item_price =
          //   parseFloat(item.currency_rate) * parseFloat(data.item_price);
          // data.sub_total_discount =
          //   parseFloat(item.currency_rate) * parseFloat(data.sub_total_discount);
          // data.sub_total_tax =
          //   parseFloat(item.currency_rate) * parseFloat(data.sub_total_tax);
          // data.sub_total =
          //   parseFloat(item.currency_rate) * parseFloat(data.sub_total);

          excelDataSource.push({
            ...item,
            status_string,
            payment_status_string,
            ...data
          });
        });
      });
    }

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const { loading, loadingForeign } = this.props.salesInvoiceDetailReport;

    const fileName = getLanguage({
      id: "salesInvoiceDetailReports.pageHeader"
    });
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="salesInvoiceDetailReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              columns={columns}
              componentRef={ref => (this.componentRef = ref)}
              data={dataSource}
              defaultExpandAllRows={true}
              expandedRowKeys={expandedRowKeys}
              expandedRowRender={record =>
                expandedRowRender(record.sales_invoice_details)
              }
              filter={filter_select}
              locations={locations}
              title={<IntlMessages id="salesInvoiceDetailReports.pageHeader" />}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() =>
                (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "zoom: 0.77;" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              defaultExpandAllRows={true}
              expandedRowKeys={expandedRowKeys}
              expandedRowRender={record =>
                expandedRowRender(record.sales_invoice_details)
              }
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              margin=".3cm"
              scale={0.43}
              title="salesInvoiceDetailReports.pageHeader"
            />

            <ExcelWrapper
              title="salesInvoiceDetailReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={excelColumns}
              data={excelDataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "transactionNo" }) +
                ", " +
                getLanguage({ id: "createdBy" }) +
                ", " +
                getLanguage({ id: "processedBy" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                disabled={
                  !permissions.includes("Sales Invoice Detail Report")
                    ? true
                    : false
                }
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="customers.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_customer"
                      )}
                      value={
                        this.state.filter.id_customer
                          ? this.state.filter.id_customer
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="select.customer" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {customers.map((data, key) => (
                        <Option key={key} value={data.id_customer}>
                          {`${data.customer_code} (${data.customer_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesInvoiceReports.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transaction_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="transaction_date">
                            <IntlMessages id="salesInvoiceReports.filter.date" />
                          </Option>
                          <Option key={2} value="sales_invoice_no">
                            <IntlMessages id="salesInvoiceNo" />
                          </Option>
                          <Option key={3} value="location_name">
                            <IntlMessages id="sidebar.location" />
                          </Option>
                          <Option key={4} value="customer_name">
                            <IntlMessages id="sidebar.customer" />
                          </Option>
                          <Option key={5} value="total_amount">
                            <IntlMessages id="totalAmount" />
                          </Option>
                          <Option key={6} value="total_qty">
                            <IntlMessages id="totalQuantity" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Row justify="space-between" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="paymentStatus" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "payment_status"
                          )}
                          value={
                            this.state.filter.payment_status
                              ? this.state.filter.payment_status
                              : []
                          }
                          style={{ width: "95%" }}
                          placeholder={getLanguage({ id: "paymentStatus" })}
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value="0">
                            {getLanguage({ id: "new" })}
                          </Option>
                          <Option value={1}>
                            {getLanguage({ id: "billed" })}
                          </Option>
                          <Option value={2}>
                            {getLanguage({ id: "paid" })}
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>{getLanguage({ id: "status" })}</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "status"
                          )}
                          value={
                            this.state.filter.status
                              ? this.state.filter.status
                              : []
                          }
                          style={{ width: "90%" }}
                          placeholder={getLanguage({ id: "status" })}
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={1}>
                            {getLanguage({ id: "pending" })}
                          </Option>
                          <Option value={2}>
                            {getLanguage({ id: "processed" })}
                          </Option>
                          <Option value={3}>
                            {getLanguage({ id: "cancelled" })}
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                  <Col span={12}>
                    <Row gutter={1} type="flex">
                      <Col
                        span={8}
                        style={{
                          marginTop: "24px"
                        }}
                      >
                        <Button
                          disabled={
                            !permissions.includes("Sales Invoice Detail Report")
                              ? true
                              : false
                          }
                          onClick={this.handleFilter}
                          size="small"
                          style={{ marginRight: "10px" }}
                          type="primary"
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) : sales_invoice_detail_reports.length === 0 &&
              !this.props.salesInvoiceDetailReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="salesInvoiceDetailReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    bordered={true}
                    className="invoiceListTable"
                    columns={columns}
                    dataSource={dataSource}
                    defaultExpandAllRows={true}
                    expandedRowRender={record =>
                      expandedRowRender(record.sales_invoice_details)
                    }
                    loading={loading}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    salesInvoiceDetailReport: state.salesInvoiceDetailReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  SalesInvoiceDetailReport
);
